import React from 'react'
import Box from './Box'

const Board = ({ children, ...props }) => {
  return (
    <Box
      width="100%"
      my="1em"
      borderRadius="1em"
      background="linear-gradient(rgba(255, 255, 255) 50%, rgba(255, 255, 255, 0))"
    >
      {children}
    </Box>
  )
}

export default Board
